import store from 'store'

const USERID = 'userid'
export default {
    //
    saveUserId(userid) {
        // localStorage.setItem(USERID,JSON.stringify(userid))
        store.set(USERID, userid)
    },
    //
    getUserId() {
        // return JSON.parse(localStorage.getItem(USERID) || '{}')
        return store.get(USERID) || ""
    },
    //
    deleteUserId() {
        // localStorage.removeItem(USERID)
        store.remove(USERID)
    }
}